.prod-bg {
  background-image: url("/images/bg-curve-mobile.svg");
  background-position: top;
  background-repeat: no-repeat;
  background-size: 500% 100%;
}
@media (min-width: 40em) {
  .prod-bg {
    background-image: url("/images/bg-curve-desktop.svg");
    background-position: top;
    background-repeat: repeat-y;
    background-size: 450% 100%;
  }
}

.productivity {
  padding-top: 100px;
  padding-bottom: 4.375rem;
  text-align: justify;
}
.productivity__img {
  width: 100%;
}
@media (max-width: 71.8125em) {
  .productivity__header {
    text-align: center;
  }
}
.productivity-link {
  height: 6.25rem;
}
@media (min-width: 71.875em) {
  .productivity-link {
    justify-content: flex-start;
  }
}
.productivity__link {
  border-bottom: 2px solid;
  font-size: 0.875rem;
  color: var(--moderate-cyan);
}
.productivity-card {
  max-width: 21.875rem;
  margin: 0 auto;
  padding: 1.875rem;
  font-size: 0.875rem;
  box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.15);
}
.productivity-card p {
  margin-top: 0;
  margin-bottom: 1.25rem;
  line-height: 1.5;
}
.productivity-card-username {
  gap: 0.625rem;
  font-size: 0.625rem;
}
.productivity-card-username img {
  width: 3.125rem;
  border-radius: 50%;
}
.productivity-card-username span {
  font-weight: bold;
  font-size: 0.875rem;
}
@media (min-width: 71.875em) {
  .productivity-card {
    margin: 0;
  }
}